import * as React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Project from "@lekoarts/gatsby-theme-jodie/src/components/blog"

const BlogPage = () => {
  const data = useStaticQuery(graphql`
  query BlogArt {
    allProject(sort: { fields: date, order: DESC }, filter: {type: {eq: "Blog"}}) {
      nodes {
        shortTitle
        slug
        type
        cover {
          childImageSharp {
            fluid(quality: 90, maxWidth: 1200) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    }
  }
`)
  return <Project data={data.allProject}></Project>
}

export default BlogPage

