import * as React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Page from "@lekoarts/gatsby-theme-jodie/src/components/last"

const PageLast = () => {
  const data = useStaticQuery(graphql`
  query LastPage {
    allProject(sort: { fields: date, order: DESC }, limit:1, filter: {type: {eq: "Blog"} }) {
      nodes {
        title
        slug
        excerpt
        date(formatString: "DD.MM.YYYY")
        body
        color
        cover {
          childImageSharp {
            fluid(quality: 90, maxWidth: 1200) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    }
  }
`)
  return <Page data={data.allProject.nodes[0]}></Page>
}

export default PageLast

